import routerDataFn from './configs/routerData';

window.MODULE_REGISTER = Object.assign(window.MODULE_REGISTER || {}, {
  basicConfigModules: {
    getRouterData: routerDataFn,
    loader: path => import(
      /* webpackMode 用于定义异步加载策略，可选 'lazy' 'lazy-once' 'eager' 'weak' */
      /* ReferTo：https://webpack.js.org/api/module-methods/#import */
    //  webpackMode: "lazy-once",
      `./basicConfigModules/${path}`
    ).then(res => {
        if (typeof res === 'function') return res;
        if (res.default) return res.default;
        return res;
    })
  },
});